import React, { useState } from 'react';

import CloseIcon from '@mui/icons-material/Close';
import SearchIcon from '@mui/icons-material/Search';
import { IconButton, InputBase, Paper, Divider } from '@mui/material';
import axios from 'axios';
import styled from 'styled-components';

import { token } from '../../configs/config';
import { useRepoStore } from '../../store/useRepoStore';

import WarningAlert from './WarningAlert';

/*  */
const SearchInputBox = ({ keyword, setKeyword, page }) => {
  /*  Repository setter  () */
  const { setRepos } = useRepoStore(state => state);
  const [open, setOpen] = useState(false); //   alert

  /* Enter      */
  const handleOnKeyPress = event => {
    if (event.key === 'Enter') {
      handleClickSearch(event);
    }
  };

  /*  Input Value onChange  */
  const handleChangeSearch = event => {
    setKeyword(event.target.value);
  };

  /*    (Github API ) */
  const handleClickSearch = async event => {
    event.preventDefault();
    if (keyword) {
      const config = {
        params: {
          q: keyword,
          per_page: 21,
          page: page,
        },
      };
      if (token) {
        config['headers'] = { Authorization: `Bearer ${token}` };
      }
      try {
        /*    API  */
        const response = await axios.get(
          'https://api.github.com/search/repositories',
          config,
        );
        /*      */
        setRepos(response.data.items);
      } catch (error) {
        if (error.response.status >= 500) {
          setOpen(true);
        }
      }
    }
  };

  /* 'X'    clear */
  const handleClickClear = event => {
    event.preventDefault();
    setKeyword('');
  };

  return (
    <>
      <SearchInputPaper
        component="form"
        sx={{
          p: '2px 4px',
          display: 'flex',
          alignItems: 'center',
          width: 'inherit',
        }}
      >
        <InputBase
          sx={{ ml: 1, flex: 1 }}
          placeholder="  !"
          value={keyword}
          autoFocus
          onChange={handleChangeSearch}
          onKeyPress={handleOnKeyPress}
          inputProps={{ 'aria-label': 'search google maps' }}
        />
        {keyword.length > 0 ? (
          <IconButton
            type="button"
            sx={{ p: '10px' }}
            aria-label="search"
            onClick={handleClickClear}
          >
            <CloseIcon />
          </IconButton>
        ) : (
          ''
        )}
        <Divider sx={{ height: 28, m: 0.5 }} orientation="vertical" />
        <IconButton
          type="button"
          sx={{ p: '10px' }}
          aria-label="search"
          onClick={handleClickSearch}
        >
          <SearchIcon />
        </IconButton>
      </SearchInputPaper>
      <WarningAlert
        open={open}
        setOpen={setOpen}
        message="   .    ."
      />
    </>
  );
};

/*   */
const SearchInputPaper = styled(Paper)`
  && {
    box-shadow: 0px 0px 0px;
    border: 1px solid #d6d6d6;
  }
`;

export default SearchInputBox;
