import React, { useEffect, useRef, useState } from 'react';

import { CircularProgress, Grid } from '@mui/material';
import axios from 'axios';

import { token } from '../../configs/config';
import { useRepoStore } from '../../store/useRepoStore';

import RepoCard from './RepoCard';
import WarningAlert from './WarningAlert';

/*  - ' '  */
const SearchResultBox = ({ repoRefs, keyword, page, setPage }) => {
  /*  Repository   () */
  const { repos, setRepos } = useRepoStore(state => state);
  /*   element  () */
  const [bottom, setBottom] = useState(null);

  /*    DOM  ref  () */
  const bottomObserver = useRef();

  const [open, setOpen] = useState(false); //   alert
  const perPage = 21;

  /*    */
  const getGithubRepos = async _page => {
    try {
      const config = {
        params: {
          q: keyword,
          per_page: 21,
          page: page,
        },
      };
      if (token) {
        config['headers'] = { Authorization: `Bearer ${token}` };
      }
      const response = await axios.get(
        'https://api.github.com/search/repositories',
        config,
      );
      /*       */
      setRepos([...repos].concat(response.data.items));
    } catch (error) {
      if (error.response.status >= 500) {
        setOpen(true);
      }
    }
  };

  useEffect(() => {
    /* 
      Intersection Observer ()
      -   ref      page+=1
    */
    const observer = new IntersectionObserver(
      entries => {
        if (entries[0].isIntersecting) {
          setPage(prev => prev + 1);
        }
      },
      { threshold: 0.5, rootMargin: '10px' },
    );
    bottomObserver.current = observer;
  });

  useEffect(() => {
    /* ref      */
    const observer = bottomObserver.current;
    if (bottom) {
      observer.observe(bottom);
    }
    return () => {
      if (bottom) {
        observer.unobserve(bottom);
      }
    };
  }, [bottom]);

  useEffect(() => {
    /* 
      Intersection Observer    
        
    */
    if (keyword) {
      /*    ( )   X */
      getGithubRepos(page);
    }
  }, [page]);

  return (
    <>
      <Grid
        container
        style={{
          maxHeight: '50vh',
          overflow: 'auto',
          marginTop: '20px',
          rowGap: '16px',
          justifyContent: 'space-evenly',
        }}
      >
        {repos.map((repo, idx) => {
          return (
            <Grid
              key={repo.full_name}
              item
              style={{ padding: '0px 8px' }}
              xs={12}
              sm={12}
              md={4}
              lg={4}
              xl={4}
              ref={element => {
                //     ref
                repoRefs.current[`repo-${repo.id}`] = element;
              }}
            >
              <RepoCard details={repo} refs={repoRefs} />
            </Grid>
          );
        })}
        {repos.length > 0 && repos.length >= perPage ? (
          //    ref
          <CircularProgress ref={setBottom} color="inherit" />
        ) : (
          ''
        )}
      </Grid>
      <WarningAlert
        open={open}
        setOpen={setOpen}
        message="   .    ."
      />
    </>
  );
};

export default SearchResultBox;
